import React from 'react';
import myStyle from "./my.module.css";
import { BiChevronLeft } from "react-icons/bi";
import { useNavigate } from 'react-router-dom';

const MyReview = () => {
  const navigate = useNavigate();

  const handleCancel = () => {
    const confirmResult = window.confirm('마이페이지로 이동 하시겠습니까?');
    if (confirmResult) {
      // 확인을 누르면 이전 페이지로 이동
      navigate(-1);
    }
  };

  return (  
      <div className={myStyle.paymentlistBox}>
       <div className={myStyle.paymentlistTop}>
      <button onClick={handleCancel}><BiChevronLeft /></button>
        <h2>내 리뷰</h2>
      </div>
      <p>현재 작성된 리뷰가 없습니다</p>
    </div>
  );
};

export default MyReview;
